/* Modern Light Dashboard with Glassmorphism */
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700&display=swap');

/* Modern Light Theme with Glassmorphism */
:root {
  /* Darker structured background like the reference */
  --primary-bg: 
    radial-gradient(circle at 20% 50%, rgba(220, 220, 230, 0.3) 0%, transparent 50%),
    radial-gradient(circle at 80% 20%, rgba(200, 200, 220, 0.4) 0%, transparent 50%),
    radial-gradient(circle at 40% 80%, rgba(180, 180, 210, 0.2) 0%, transparent 50%),
    linear-gradient(135deg, #e2e8f0 0%, #cbd5e1 50%, #e2e8f0 100%);
  
  --secondary-bg: rgba(255, 255, 255, 0.4);
  --card-bg: rgba(255, 255, 255, 0.25); /* More transparent cards */
  --surface-bg: rgba(248, 250, 252, 0.6);
  
  /* Text Colors */
  --text-primary: #1e293b;
  --text-secondary: #64748b;
  --text-muted: #94a3b8;
  --text-chart: #6b7280; /* Neutrální šedá pro grafy */
  
  /* Accent Colors */
  --accent-purple: #8b5cf6;
  --accent-green: #10b981;
  --accent-red: #ef4444;
  --accent-blue: #3b82f6;
  --accent-orange: #f59e0b;
  
  /* Borders and Effects */
  --border-color: rgba(226, 232, 240, 0.4);
  --shadow-light: 0 1px 3px rgba(0, 0, 0, 0.05);
  --shadow-md: 0 4px 6px rgba(0, 0, 0, 0.07);
  --shadow-lg: 0 10px 15px rgba(0, 0, 0, 0.1);
  --glass-border: rgba(255, 255, 255, 0.3);
  
  /* Typography */
  --font-primary: 'Inter', system-ui, -apple-system, BlinkMacSystemFont, sans-serif;
  
  /* Enhanced Glassmorphism */
  --glass-bg: rgba(255, 255, 255, 0.15);
  --glass-border: rgba(255, 255, 255, 0.25);
  --backdrop-blur: blur(25px);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: var(--font-primary);
  background: var(--primary-bg);
  color: var(--text-primary);
  overflow-x: hidden;
  font-weight: 400;
  letter-spacing: -0.01em;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  min-height: 100vh;
  position: relative;
}

/* Soft flowing wave background */
body::before {
  content: '';
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: 
    radial-gradient(circle at 1px 1px, rgba(255,255,255,0.15) 1px, transparent 0);
  background-size: 20px 20px;
  pointer-events: none;
  z-index: -2;
}

/* VELMI VIDITELNÁ gradient wave */
body::after {
  content: '';
  position: fixed;
  top: 0;
  right: 0;
  width: 60%;
  height: 100%;
  background: 
    linear-gradient(135deg, 
      rgba(147, 197, 253, 0.8) 0%, 
      rgba(239, 168, 218, 0.7) 50%, 
      rgba(243, 244, 246, 0.6) 100%
    );
  clip-path: ellipse(80% 100% at 100% 50%);
  pointer-events: none;
  z-index: 1;
}

/* VELMI VIDITELNÁ dolní vlna */
.app-container::before {
  content: '';
  position: fixed;
  bottom: 0;
  left: 0;
  width: 50%;
  height: 60%;
  background: 
    linear-gradient(45deg, 
      rgba(165, 180, 252, 0.7) 0%, 
      rgba(251, 207, 232, 0.6) 70%, 
      transparent 100%
    );
  clip-path: ellipse(100% 80% at 0% 100%);
  pointer-events: none;
  z-index: 1;
}

@keyframes gentle-float {
  0%, 100% {
    transform: rotate(-15deg) translateY(0px) scale(1);
    opacity: 0.8;
  }
  33% {
    transform: rotate(-12deg) translateY(-20px) scale(1.02);
    opacity: 0.6;
  }
  66% {
    transform: rotate(-18deg) translateY(15px) scale(0.98);
    opacity: 0.7;
  }
}

.app-container {
  display: flex;
  min-height: 100vh;
  background: var(--primary-bg);
  position: relative;
}

/* Soft gradient overlay */
.app-container::after {
  content: '';
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: 
    linear-gradient(135deg, 
      rgba(248, 250, 252, 0.4) 0%, 
      rgba(241, 245, 249, 0.3) 25%, 
      rgba(243, 244, 246, 0.2) 50%, 
      rgba(249, 250, 251, 0.3) 75%, 
      rgba(248, 250, 252, 0.4) 100%
    );
  pointer-events: none;
  z-index: 0;
}

/* Sidebar Styles */
.sidebar {
  width: 280px;
  background: var(--glass-bg);
  backdrop-filter: var(--backdrop-blur);
  -webkit-backdrop-filter: var(--backdrop-blur);
  border-right: 1px solid var(--glass-border);
  padding: 32px 24px;
  position: fixed;
  left: 0;
  top: 0;
  height: 100vh;
  z-index: 200;
  box-shadow: var(--shadow-md);
}

.sidebar-header {
  padding: 32px 24px;
  border-bottom: 1px solid var(--border-color);
}

.logo {
  display: flex;
  align-items: center;
  gap: 16px;
}

.logo-icon {
  width: 48px;
  height: 48px;
  background: var(--gradient-primary);
  border-radius: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 24px;
  box-shadow: var(--shadow-sm);
}

.logo-text {
  display: flex;
  flex-direction: column;
}

.logo-title {
  font-size: 22px;
  font-weight: 700;
  color: var(--text-primary);
  margin-bottom: 2px;
}

.logo-subtitle {
  font-size: 12px;
  font-weight: 500;
  color: var(--text-muted);
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.sidebar-nav {
  flex: 1;
  padding: 24px;
}

.nav-item {
  display: flex;
  align-items: center;
  gap: 16px;
  padding: 14px 20px;
  border-radius: 16px;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  font-weight: 500;
  font-size: 15px;
  color: var(--text-secondary);
  margin-bottom: 4px;
  position: relative;
}

.nav-item:hover {
  background: rgba(139, 92, 246, 0.08);
  color: var(--accent-purple);
  transform: translateX(4px);
}

.nav-item.active {
  background: rgba(139, 92, 246, 0.12);
  color: var(--accent-purple);
  box-shadow: var(--shadow-light);
}

.nav-item.active::before {
  content: '';
  position: absolute;
  left: -24px;
  top: 50%;
  transform: translateY(-50%);
  width: 4px;
  height: 24px;
  background: var(--accent-blue);
  border-radius: 2px;
}

.nav-icon {
  font-size: 20px;
  width: 24px;
  height: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* Page Layout with enhanced background */
.main-content {
  flex: 1;
  margin-left: 280px;
  padding: 40px 48px;
  background: transparent;
  min-height: 100vh;
  position: relative;
  z-index: 100;
}

/* Transparent chart cards */
.chart-card {
  background: rgba(255, 255, 255, 0.12);
  backdrop-filter: blur(25px);
  -webkit-backdrop-filter: blur(25px);
  border-radius: 24px;
  padding: 32px;
  border: 1px solid rgba(255, 255, 255, 0.18);
  box-shadow: 
    0 8px 32px rgba(0, 0, 0, 0.04),
    0 1px 2px rgba(0, 0, 0, 0.08);
  transition: all 0.3s ease;
}

.chart-card:hover {
  background: rgba(255, 255, 255, 0.18);
  transform: translateY(-2px);
  box-shadow: 
    0 20px 40px rgba(0, 0, 0, 0.08),
    0 8px 16px rgba(0, 0, 0, 0.12);
}

/* Page Header */
.page-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 32px;
}

.page-header h1 {
  font-size: 32px;
  font-weight: 600;
  color: var(--text-primary);
  margin-bottom: 8px;
  line-height: 1.2;
}

.page-header p {
  font-size: 16px;
  font-weight: 400;
  color: var(--text-secondary);
  line-height: 1.5;
}

.page-actions {
  display: flex;
  gap: 16px;
}

/* Buttons */
.btn {
  padding: 12px 24px;
  border-radius: 12px;
  border: none;
  font-family: var(--font-primary);
  font-weight: 600;
  font-size: 14px;
  letter-spacing: -0.01em;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  display: flex;
  align-items: center;
  gap: 8px;
  position: relative;
  overflow: hidden;
}

.btn::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.1), transparent);
  transition: left 0.5s;
}

.btn:hover::before {
  left: 100%;
}

.btn-primary {
  background: var(--accent-blue);
  color: var(--text-primary);
  box-shadow: var(--shadow-sm);
}

.btn-primary:hover {
  background: #3730A3;
  transform: translateY(-2px);
  box-shadow: var(--shadow-md);
}

.btn-secondary {
  background: var(--surface-bg);
  color: var(--text-primary);
  border: 1px solid var(--border-color);
}

.btn-secondary:hover {
  background: var(--card-bg);
  transform: translateY(-2px);
}

.btn-danger {
  background: var(--accent-red);
  color: var(--text-primary);
}

.btn-danger:hover {
  background: #DC2626;
  transform: translateY(-2px);
}

/* Stats Grid */
.stats-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 24px;
  margin-bottom: 32px;
}

.stat-card {
  background: var(--card-bg);
  border-radius: 16px;
  padding: 24px;
  border: 1px solid var(--border-color);
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  position: relative;
  overflow: hidden;
}

/* Enhanced Transparent Cards */
.stat-card {
  background: rgba(255, 255, 255, 0.15);
  backdrop-filter: var(--backdrop-blur);
  -webkit-backdrop-filter: var(--backdrop-blur);
  border-radius: 24px;
  padding: 32px 28px;
  border: 1px solid rgba(255, 255, 255, 0.2);
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  position: relative;
  overflow: hidden;
  box-shadow: 
    0 8px 32px rgba(0, 0, 0, 0.04),
    0 1px 2px rgba(0, 0, 0, 0.08);
}

.stat-card:hover {
  transform: translateY(-4px);
  background: rgba(255, 255, 255, 0.2);
  box-shadow: 
    0 20px 40px rgba(0, 0, 0, 0.08),
    0 8px 16px rgba(0, 0, 0, 0.12);
  border-color: rgba(139, 92, 246, 0.3);
}

/* Soft geometric decoration */
.geometric-decoration {
  position: absolute;
  top: 0;
  right: 0;
  width: 100px;
  height: 100px;
  pointer-events: none;
  overflow: hidden;
  z-index: 1;
}

.geometric-decoration .circle {
  position: absolute;
  border-radius: 50%;
  background: rgba(139, 92, 246, 0.04);
  border: 1px solid rgba(139, 92, 246, 0.08);
}

.geometric-decoration .circle-1 {
  width: 70px;
  height: 70px;
  top: -15px;
  right: -15px;
  background: rgba(139, 92, 246, 0.06);
  border: 1px solid rgba(139, 92, 246, 0.12);
}

.geometric-decoration .circle-2 {
  width: 50px;
  height: 50px;
  top: 10px;
  right: 15px;
  background: rgba(16, 185, 129, 0.04);
  border: 1px solid rgba(16, 185, 129, 0.08);
}

.geometric-decoration .circle-3 {
  width: 35px;
  height: 35px;
  top: 35px;
  right: -5px;
  background: rgba(59, 130, 246, 0.03);
  border: 1px solid rgba(59, 130, 246, 0.06);
}

.stat-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 2px;
  background: var(--gradient-primary);
  transform: scaleX(0);
  transition: transform 0.3s;
}

.stat-card:hover::before {
  transform: scaleX(1);
}

.stat-card:hover,
.stat-card.hovered {
  transform: translateY(-8px);
  box-shadow: var(--shadow-lg);
  border-color: var(--accent-blue);
}

.stat-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
}

.stat-title {
  font-size: 12px;
  font-weight: 500;
  color: var(--text-muted);
  text-transform: uppercase;
  letter-spacing: 0.5px;
  margin-bottom: 8px;
}

.stat-content {
  position: relative;
  z-index: 2;
}

.stat-value-row {
  display: flex;
  align-items: flex-start;
  gap: 12px;
  margin-bottom: 0;
}

.stat-value-with-subtitle {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.stat-icon {
  width: 20px;
  height: 20px;
  border-radius: 6px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 10px;
  flex-shrink: 0;
  position: relative;
  margin-top: 2px;
}

.stat-icon.icon-money {
  background: rgba(59, 130, 246, 0.15);
  color: var(--accent-blue);
}

.stat-icon.icon-chart {
  background: rgba(16, 185, 129, 0.15);
  color: var(--accent-green);
}

.stat-icon.icon-orders {
  background: rgba(139, 92, 246, 0.15);
  color: var(--accent-purple);
}

.stat-icon.icon-target {
  background: rgba(245, 158, 11, 0.15);
  color: var(--accent-orange);
}

/* Moderní geometrické ikony místo emoji - bez stínů */
.stat-icon.icon-money::before { 
  content: "";
  width: 8px;
  height: 8px;
  border: 2px solid currentColor;
  border-radius: 50%;
  display: block;
}

.stat-icon.icon-chart::before { 
  content: "";
  width: 3px;
  height: 8px;
  background: currentColor;
  border-radius: 1px;
}

.stat-icon.icon-chart::after { 
  content: "";
  width: 3px;
  height: 6px;
  background: currentColor;
  border-radius: 1px;
  position: absolute;
  left: 7px;
  top: 8px;
}

.stat-icon.icon-orders::before { 
  content: "";
  width: 8px;
  height: 1.5px;
  background: currentColor;
  border-radius: 1px;
}

.stat-icon.icon-orders::after { 
  content: "";
  width: 8px;
  height: 1.5px;
  background: currentColor;
  border-radius: 1px;
  position: absolute;
  top: 12px;
}

.stat-icon.icon-target::before { 
  content: "";
  width: 6px;
  height: 6px;
  border: 2px solid currentColor;
  border-radius: 50%;
  position: relative;
}

.stat-icon.icon-target::after { 
  content: "";
  width: 2px;
  height: 2px;
  background: currentColor;
  border-radius: 50%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.stat-icon {
  width: 48px;
  height: 48px;
  border-radius: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 24px;
  flex-shrink: 0;
}

.stat-icon.blue { background: rgba(79, 70, 229, 0.2); }
.stat-icon.green { background: rgba(16, 185, 129, 0.2); }
.stat-icon.purple { background: rgba(139, 92, 246, 0.2); }
.stat-icon.orange { background: rgba(245, 158, 11, 0.2); }

.stat-value {
  font-size: 32px;
  font-weight: 700;
  color: var(--text-primary);
  margin-bottom: 4px;
  line-height: 1.1;
}

.stat-subtitle {
  font-size: 14px;
  font-weight: 400;
  color: var(--text-secondary);
  line-height: 1.4;
  margin-top: 2px;
}

.stat-subtitle.blue {
  color: var(--accent-blue);
  font-weight: 500;
}

.stat-subtitle.small {
  font-size: 11px;
  font-weight: 400;
  color: var(--text-muted);
}

/* Charts */
.charts-grid {
  display: grid;
  grid-template-columns: 2fr 1fr;
  gap: 24px;
  margin-bottom: 32px;
}

.chart-card {
  background: var(--card-bg);
  border-radius: 16px;
  padding: 24px;
  border: 1px solid var(--border-color);
  transition: all 0.3s;
}

.chart-card:hover {
  transform: translateY(-4px);
  box-shadow: var(--shadow-md);
}

.chart-card.large {
  grid-column: span 1;
}

.chart-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 24px;
}

.chart-header h3 {
  font-family: var(--font-primary);
  font-size: 14px;
  font-weight: 600;
  color: var(--text-muted);
  letter-spacing: 0.05em;
  text-transform: uppercase;
  margin-bottom: 8px;
}

.chart-values-dual {
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.chart-value-main {
  font-family: var(--font-heading);
  font-size: 24px;
  font-weight: 700;
  color: var(--text-primary);
  line-height: 1.2;
  letter-spacing: -0.02em;
}

.chart-value-secondary {
  font-family: var(--font-primary);
  font-size: 14px;
  font-weight: 500;
  color: var(--text-secondary);
  line-height: 1.2;
  letter-spacing: -0.01em;
}

.chart-value {
  font-family: var(--font-heading);
  font-size: 24px;
  font-weight: 700;
  color: var(--text-primary);
  letter-spacing: -0.02em;
}

.chart-container-large {
  height: 380px;
  position: relative;
}

/* 3D Donut Chart with External Labels */
.chart-container-donut {
  height: 350px;
  position: relative;
  padding: 24px;
  /* Standardní card styling jako u ostatních */
}

.donut-chart-wrapper {
  position: relative;
  height: 100%;
  width: 100%;
  filter: drop-shadow(0 10px 20px rgba(0, 0, 0, 0.3));
}

.external-labels {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  pointer-events: none;
}

.label-item {
  position: absolute;
  display: flex;
  align-items: center;
  color: var(--text-secondary);
  font-weight: 600;
  font-size: 13px;
}

.label-top-left {
  top: 15%;
  left: 15%;
  transform: translate(-50%, -50%);
}

.label-top-right {
  top: 15%;
  right: 15%;
  transform: translate(50%, -50%);
}

.label-bottom-left {
  bottom: 15%;
  left: 15%;
  transform: translate(-50%, 50%);
}

.label-bottom-right {
  bottom: 15%;
  right: 15%;
  transform: translate(50%, 50%);
}

.label-line {
  width: 30px;
  height: 1px;
  background: linear-gradient(90deg, rgba(255, 255, 255, 0.6) 0%, rgba(255, 255, 255, 0.2) 100%);
  margin-right: 12px;
}

.label-content {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.label-percentage {
  font-size: 16px;
  font-weight: 700;
  color: var(--text-primary);
  margin-bottom: 2px;
  text-shadow: 0 2px 4px rgba(0, 0, 0, 0.3);
}

.label-name {
  font-size: 11px;
  color: var(--text-muted);
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.label-top-left {
  top: 15%;
  left: 15%;
  transform: translate(-50%, -50%);
}

.label-top-right {
  top: 15%;
  right: 15%;
  transform: translate(50%, -50%);
}

.label-bottom-left {
  bottom: 15%;
  left: 15%;
  transform: translate(-50%, 50%);
}

.label-bottom-right {
  bottom: 15%;
  right: 15%;
  transform: translate(50%, 50%);
}

.label-line {
  width: 30px;
  height: 1px;
  background: linear-gradient(90deg, rgba(255, 255, 255, 0.3), rgba(255, 255, 255, 0.8));
  margin: 0 8px;
  position: relative;
}

.label-line::after {
  content: '';
  position: absolute;
  right: -2px;
  top: -1.5px;
  width: 4px;
  height: 4px;
  background: rgba(255, 255, 255, 0.9);
  border-radius: 50%;
}

.label-top-right .label-line::after,
.label-bottom-right .label-line::after {
  right: auto;
  left: -2px;
}

.label-content {
  text-align: center;
  background: rgba(255, 255, 255, 0.15);
  padding: 8px 12px;
  border-radius: 8px;
  backdrop-filter: blur(25px);
  -webkit-backdrop-filter: blur(25px);
  border: 1px solid rgba(255, 255, 255, 0.25);
  min-width: 60px;
}

.label-percentage {
  font-family: var(--font-heading);
  font-size: 18px;
  font-weight: 700;
  color: var(--text-primary);
  line-height: 1;
  margin-bottom: 2px;
  letter-spacing: -0.02em;
}

.label-name {
  font-family: var(--font-primary);
  font-size: 11px;
  font-weight: 500;
  color: var(--text-secondary);
  text-transform: uppercase;
  letter-spacing: 0.05em;
}

/* Form styling */
.form-group label {
  font-family: var(--font-primary);
  font-size: 12px;
  font-weight: 500;
  color: var(--text-secondary);
  text-transform: uppercase;
  letter-spacing: 0.05em;
}

.form-group input,
.form-group select,
.form-group textarea {
  font-family: var(--font-primary);
  padding: 12px 16px;
  border-radius: 8px;
  border: 1px solid var(--border-color);
  background: var(--surface-bg);
  color: var(--text-primary);
  font-size: 14px;
  font-weight: 400;
  letter-spacing: -0.01em;
  transition: all 0.2s;
}

/* Table styling */
