}

.settings-card-header {
  display: flex;
  align-items: center;
  gap: 16px;
  margin-bottom: 24px;
}

.settings-icon {
  width: 48px;
  height: 48px;
  border-radius: 12px;
  background: var(--surface-bg);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 24px;
  color: var(--text-primary);
}

.settings-card-header h3 {
  font-size: 18px;
  font-weight: 600;
  color: var(--text-primary);
  flex: 1;
}

.settings-actions {
  display: flex;
  gap: 8px;
}

.btn-settings {
  padding: 6px 12px;
  border-radius: 6px;
  border: 1px solid var(--border-color);
  background: var(--surface-bg);
  color: var(--text-secondary);
  font-size: 11px;
  font-weight: 600;
  letter-spacing: 0.5px;
  text-transform: uppercase;
  cursor: pointer;
  transition: all 0.2s;
}

.btn-settings:hover {
  background: var(--accent-blue);
  color: var(--text-primary);
  border-color: var(--accent-blue);
}

.settings-badge {
  padding: 6px 12px;
  border-radius: 6px;
  background: var(--accent-green);
  color: var(--text-primary);
  font-size: 11px;
  font-weight: 600;
  letter-spacing: 0.5px;
  text-transform: uppercase;
}

.settings-form {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.form-row {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;
}

.form-group {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.form-group label {
  font-size: 12px;
  font-weight: 500;
  color: var(--text-secondary);
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.form-group input,
.form-group select,
.form-group textarea {
  padding: 12px 16px;
  border-radius: 8px;
  border: 1px solid var(--border-color);
  background: var(--surface-bg);
  color: var(--text-primary);
  font-size: 14px;
  transition: all 0.2s;
}

.form-group input:focus,
.form-group select:focus,
.form-group textarea:focus {
  outline: none;
  border-color: var(--accent-blue);
  box-shadow: 0 0 0 3px rgba(79, 70, 229, 0.1);
}

.security-note {
  padding: 16px;
  background: rgba(245, 158, 11, 0.1);
  border-radius: 8px;
  border-left: 4px solid var(--accent-orange);
  font-size: 12px;
  color: var(--text-secondary);
  line-height: 1.5;
}

.settings-actions-footer {
  display: flex;
  justify-content: flex-end;
  gap: 16px;
  padding-top: 24px;
  border-top: 1px solid var(--border-color);
}

/* Reports 2x2 Grid Layout */
.charts-grid-4 {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 24px;
  margin-bottom: 32px;
}

.chart-card-small {
  background: rgba(255, 255, 255, 0.12);
  backdrop-filter: blur(25px);
  -webkit-backdrop-filter: blur(25px);
  border-radius: 20px;
  padding: 24px;
  border: 1px solid rgba(255, 255, 255, 0.18);
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  position: relative;
  overflow: hidden;
  box-shadow: 
    0 8px 32px rgba(0, 0, 0, 0.04),
    0 1px 2px rgba(0, 0, 0, 0.08);
}

.chart-card-small:hover {
  transform: translateY(-4px);
  background: rgba(255, 255, 255, 0.18);
  box-shadow: 
    0 20px 40px rgba(0, 0, 0, 0.08),
    0 8px 16px rgba(0, 0, 0, 0.12);
  border-color: rgba(139, 92, 246, 0.3);
}

.chart-header-small {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
}

.chart-header-small h3 {
  font-size: 12px;
  font-weight: 600;
  color: var(--text-muted);
  letter-spacing: 0.5px;
  text-transform: uppercase;
}

.chart-value-small {
  font-size: 20px;
  font-weight: 700;
  line-height: 1.2;
}

.chart-value-small.blue { color: var(--accent-blue); }
.chart-value-small.green { color: var(--accent-green); }
.chart-value-small.orange { color: var(--accent-orange); }
.chart-value-small.purple { color: var(--accent-purple); }

.chart-container-small {
  height: 200px;
  position: relative;
}

/* 5 Stats Row */
.stats-row {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  gap: 20px;
  margin-bottom: 32px;
}

.stat-cell {
  background: var(--card-bg);
  border-radius: 16px;
  padding: 24px;
  border: 1px solid var(--border-color);
  text-align: center;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  position: relative;
  overflow: hidden;
}

.stat-cell::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 2px;
  background: var(--gradient-primary);
  transform: scaleX(0);
  transition: transform 0.3s;
}

.stat-cell:hover::before {
  transform: scaleX(1);
}

.stat-cell:hover {
  transform: translateY(-4px);
  box-shadow: var(--shadow-md);
  border-color: var(--accent-blue);
}

.stat-icon-large {
  width: 56px;
  height: 56px;
  border-radius: 16px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 28px;
  margin: 0 auto 16px;
  transition: all 0.3s;
}

.stat-icon-large.blue { background: rgba(79, 70, 229, 0.15); }
.stat-icon-large.green { background: rgba(16, 185, 129, 0.15); }
.stat-icon-large.purple { background: rgba(139, 92, 246, 0.15); }
.stat-icon-large.orange { background: rgba(245, 158, 11, 0.15); }
.stat-icon-large.red { background: rgba(239, 68, 68, 0.15); }

.stat-cell:hover .stat-icon-large {
  transform: scale(1.1);
}

.stat-value-large {
  font-size: 28px;
  font-weight: 700;
  color: var(--text-primary);
  margin-bottom: 8px;
  line-height: 1.2;
}

.stat-label-large {
  font-size: 12px;
  font-weight: 600;
  color: var(--text-muted);
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

/* Action Buttons Row */
.action-buttons-row {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 24px;
  margin-top: 32px;
}

.action-button-card {
  background: var(--card-bg);
  border-radius: 16px;
  padding: 24px;
  border: 1px solid var(--border-color);
  display: flex;
  align-items: center;
  gap: 20px;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  position: relative;
  overflow: hidden;
}

.action-button-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 3px;
  background: var(--gradient-primary);
  transform: scaleX(0);
  transition: transform 0.3s;
}

.action-button-card:hover::before {
  transform: scaleX(1);
}

.action-button-card:hover {
  transform: translateY(-6px);
  box-shadow: var(--shadow-lg);
  border-color: var(--accent-blue);
}

.action-button-icon {
  width: 56px;
  height: 56px;
  border-radius: 16px;
  background: var(--surface-bg);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 28px;
  transition: all 0.3s;
  flex-shrink: 0;
}

.action-button-card:hover .action-button-icon {
  background: var(--accent-blue);
  transform: scale(1.1);
}

.action-button-content {
  flex: 1;
}

.action-button-title {
  font-size: 18px;
  font-weight: 600;
  color: var(--text-primary);
  margin-bottom: 4px;
}

.action-button-subtitle {
  font-size: 14px;
  color: var(--text-secondary);
  line-height: 1.4;
}

/* Reports Full Width Layout */
.report-main-full {
  margin-bottom: 32px;
}

.chart-card-full {
  background: rgba(255, 255, 255, 0.12);
  backdrop-filter: blur(25px);
  -webkit-backdrop-filter: blur(25px);
  border-radius: 20px;
  padding: 32px;
  border: 1px solid rgba(255, 255, 255, 0.18);
  transition: all 0.3s ease;
  box-shadow: 
    0 8px 32px rgba(0, 0, 0, 0.04),
    0 1px 2px rgba(0, 0, 0, 0.08);
}

.chart-card-full:hover {
  transform: translateY(-4px);
  background: rgba(255, 255, 255, 0.18);
  box-shadow: 
    0 20px 40px rgba(0, 0, 0, 0.08),
    0 8px 16px rgba(0, 0, 0, 0.12);
}

.chart-card-full .chart-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 32px;
}

.chart-subtitle {
  font-size: 14px;
  color: var(--text-secondary);
  margin-bottom: 16px;
  font-weight: 500;
}

.chart-value-large {
  font-size: 36px;
  font-weight: 700;
  color: var(--text-primary);
  margin-bottom: 8px;
  background: var(--gradient-primary);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.chart-value-subtitle {
  font-size: 14px;
  color: var(--text-secondary);
}

.period-overview {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 16px;
  min-width: 240px;
}

.period-stat {
  text-align: center;
  padding: 16px;
  background: var(--surface-bg);
  border-radius: 12px;
  transition: all 0.3s;
}

.period-stat:hover {
  background: var(--secondary-bg);
  transform: translateY(-2px);
}

.period-label {
  font-size: 11px;
  font-weight: 600;
  color: var(--text-muted);
  text-transform: uppercase;
  letter-spacing: 0.5px;
  margin-bottom: 8px;
}

.period-value {
  font-size: 16px;
  font-weight: 700;
  line-height: 1.2;
}

.period-value.blue { color: var(--accent-blue); }
.period-value.green { color: var(--accent-green); }
.period-value.orange { color: var(--accent-orange); }
.period-value.purple { color: var(--accent-purple); }

.chart-container-large {
  height: 400px;
  position: relative;
  background: var(--surface-bg);
  border-radius: 12px;
  padding: 16px;
}

/* Modal Styles */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.7);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  animation: fadeIn 0.3s ease-out;
}

@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

.modal-content {
  background: var(--card-bg);
  border-radius: 16px;
  padding: 0;
  max-width: 600px;
  width: 90%;
  max-height: 80vh;
  overflow-y: auto;
  border: 1px solid var(--border-color);
  box-shadow: var(--shadow-lg);
  animation: slideIn 0.3s ease-out;
}

@keyframes slideIn {
  from { 
    opacity: 0;
    transform: translateY(-20px) scale(0.95);
  }
  to { 
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}

.modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 24px;
  border-bottom: 1px solid var(--border-color);
}

.modal-header h2 {
  font-size: 20px;
  font-weight: 600;
  color: var(--text-primary);
  margin: 0;
}

.modal-close {
  width: 32px;
  height: 32px;
  border-radius: 8px;
  border: none;
  background: var(--surface-bg);
  color: var(--text-secondary);
  font-size: 20px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.2s;
}

.modal-close:hover {
  background: var(--accent-red);
  color: var(--text-primary);
}

.modal-form {
  padding: 24px;
}

.modal-actions {
  display: flex;
  justify-content: flex-end;
  gap: 16px;
  padding-top: 24px;
  border-top: 1px solid var(--border-color);
  margin-top: 24px;
}

/* Mini Stats Cards */
.stats-grid-top,
.stats-grid-bottom {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 16px;
  margin-bottom: 24px;
}

.stat-card-mini {
  background: var(--card-bg);
  border-radius: 12px;
  padding: 16px;
  border: 1px solid var(--border-color);
  text-align: center;
  transition: all 0.3s;
}

.stat-card-mini:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-md);
}

.stat-title-mini {
  font-size: 11px;
  font-weight: 600;
  color: var(--text-muted);
  text-transform: uppercase;
  letter-spacing: 0.5px;
  margin-bottom: 8px;
}

.stat-value-mini {
  font-size: 18px;
  font-weight: 700;
  color: var(--text-primary);
  letter-spacing: -0.01em;
}

/* Table Amount and Profit Styling */
.amount-bold-blue {
  font-weight: 700 !important;
  color: #3B82F6 !important;
}

.profit-bold-orange {
  font-weight: 700 !important;
  color: #F59E0B !important;
}

/* Filter Actions */
.filter-actions {
  padding-top: 16px;
  border-top: 1px solid var(--border-color);
  margin-top: 16px;
}

.btn-small {
  padding: 8px 16px;
  font-size: 14px;
  border-radius: 8px;
}

/* Helper Classes */
.green { color: var(--accent-green); }
.blue { color: var(--accent-blue); }
.purple { color: var(--accent-purple); }
.orange { color: var(--accent-orange); }
.red { color: var(--accent-red); }

/* Responsive */
@media (max-width: 1024px) {
  .sidebar {
    transform: translateX(-100%);
  }
  
  .main-content {
    margin-left: 0;
  }
  
  .charts-grid {
    grid-template-columns: 1fr;
  }
  
  .activity-grid {
    grid-template-columns: 1fr;
  }
  
  .report-main {
    grid-template-columns: 1fr;
  }
  
  .stats-grid {
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  }
}

@media (max-width: 768px) {
  .main-content {
    padding: 16px;
  }
  
  .page-header {
    flex-direction: column;
    gap: 16px;
    align-items: flex-start;
  }
  
  .page-actions {
    width: 100%;
    justify-content: flex-start;
  }
  
  .form-row {
    grid-template-columns: 1fr;
  }
  
  .filter-row {
    grid-template-columns: 1fr;
  }
  
  .table-container {
    font-size: 12px;
  }
  
  .orders-table th,
  .orders-table td {
    padding: 8px 12px;
  }
}